  <div>
    <h1>TheSlider</h1>
    <small class="slider-mode"></small>
    <div class="container-wrapper">
      <div class="container">
        <div class="container-sliders">
          <div ref="sliderLarge" class="container-slider-large"></div>
        </div>
        <br />
        <div class="container-sliders p0">
          <div ref="" class="container-slider-wrapper">
            <div ref="sliderSmall1" class="container-slider"></div>
          </div>
          <div ref="" class="container-slider-wrapper">
            <div ref="sliderSmall2" class="container-slider"></div>
          </div>
          <div ref="" class="container-slider-wrapper">
            <div ref="sliderSmall3" class="container-slider"></div>
          </div>
          <div ref="" class="container-slider-wrapper">
            <div ref="sliderSmall4" class="container-slider"></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TheSlider from "../lib/bundle.js";

/* eslint-disable */
const IMAGES = [
  `09a564db-8487-4633-b3e0-e1c9c74790f0.webp`,
  `ef1030cd-65fe-4cc7-85ae-a0d498587374.webp`,
  `1c839b10-751f-45e9-8f36-765eb5c08801.webp`,
  `615754a0-e1c0-4443-b43e-6f23aef7f4ea.webp`,
  `09a1c7ac-e01b-4b10-8a4e-9a74053a4a7b.webp`,
  `18050471-7fc9-412d-ad4a-0bec7a02012e.webp`,
  `e073a226-e163-40b7-91b0-bbe1dbda9bbc.webp`,
  `954999c4-9f4a-40b7-9e8d-ad5c432060a9.webp`,
  `f9159ab3-3bfe-4d0d-9b0b-e3be770b4002.webp`,
  `60418960-2ed4-450e-bd86-63b1169382f2.webp`,
];
/* eslint-enable */

export default {
  name: "Slides",
  props: {
    msg: String,
  },
  data: function () {
    return {
      slidersEl: [],
    };
  },
  // define methods under the `methods` object
  methods: {
    getImages() {
      var arr = [];
      IMAGES.forEach((imageName) => {
        arr.push({ path: require(`@/assets/images/${imageName}`), link: "" });
      });
      return arr;
    },
    initSlider(el) {
      try {
        const theSlider = new TheSlider();
        theSlider
          .config({
            target: el,
            renderImages: 10,
            images: this.shuffle(this.getImages()), //this.getImages()
          })
          .init();
      } catch (err) {
        console.error(err);
      }
    },
    shuffle(array) {
      let currentIndex = array.length,
        randomIndex;

      // While there remain elements to shuffle...
      while (currentIndex != 0) {
        // Pick a remaining element...
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex--;

        // And swap it with the current element.
        [array[currentIndex], array[randomIndex]] = [
          array[randomIndex],
          array[currentIndex],
        ];
      }

      return array;
    },
  },
  mounted: function () {
    this.slidersEl = [
      this.$refs.sliderLarge,
      this.$refs.sliderSmall1,
      this.$refs.sliderSmall2,
      this.$refs.sliderSmall3,
      this.$refs.sliderSmall4,
    ];
    // const theSlider = new TheSlider();
    this.slidersEl.forEach((el) => {
      this.initSlider(el);
    });
  },
};
</script>

